@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Stencil+Display:wght@600&display=swap);
html {
  box-sizing: border-box;
  overflow-y: scroll;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.image img {
  min-width: 100vw;
  min-height: calc(100vh - 64px);
  object-fit: cover !important;
  box-sizing: border-box;
}
.gallery {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.Gallery_GalleryWall__diua2 {
  width: 100vw;
  max-width: 100%;
  display: grid;
  grid-gap: 1px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-rows: 350px;
  overflow: hidden;
  padding: 5px;
  padding-top: 25px;
  padding-bottom: 25px;
  margin-top: -25px;
  margin-bottom: -25px;
}
.Gallery_Box__2V7HA {
  overflow: hidden;
  position: relative;
  transition: all 0.25s;
  border: 1px solid white;
}
.Gallery_Box__2V7HA:hover {
  transform: scale(1.05);
  z-index: 9;
  border: 2px solid black;
  box-shadow: 4px 4px 6px 6px rgba(0, 0, 0, 0.7);
}
.Gallery_Image__1lLLR {
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  display: none;
}
:root {
  --delay: 0.5s;
}

.Gallery_Loaded__2mPpI {
  display: block;
  animation: Gallery_fadeIn__UcRL3 ease 0.5s;
  animation: Gallery_fadeIn__UcRL3 ease var(--delay);
  -webkit-animation: Gallery_fadeIn__UcRL3 ease 0.5s;
  -webkit-animation: Gallery_fadeIn__UcRL3 ease var(--delay);
  -moz-animation: Gallery_fadeIn__UcRL3 ease 0.5s;
  -moz-animation: Gallery_fadeIn__UcRL3 ease var(--delay);
  -o-animation: Gallery_fadeIn__UcRL3 ease 0.5s;
  -o-animation: Gallery_fadeIn__UcRL3 ease var(--delay);
  -ms-animation: Gallery_fadeIn__UcRL3 ease 0.5s;
  -ms-animation: Gallery_fadeIn__UcRL3 ease var(--delay);
}

@keyframes Gallery_fadeIn__UcRL3 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes Gallery_fadeIn__UcRL3 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

